
import applyFooter from 'components/apply/apply-footer.vue'
import {applyQuery, applyCreate, recall} from '@/api/apply-detail'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


export default {
name: 'vacationInfo',
components: {
Selector,
Group,
XTextarea,
Datetime,
Cell,
applyFooter,
XButton,
recall
},
data() {
return {
applyStatus: "1",
vacationList: ["", "", "", "", "", "", ""],
params: {
hrWorkflowId: "", //id
hrApplyType: "",
holidayStart: "",
holidayEnd: "",
holidayReason: "",
fid: ""
}
}
},
methods: {

onChange(val) {
console.log(val);
},
changeStart(time) {

},
changeEnd(time) {

},
save(){
if (this.hrApplyType === "") {
this.$vux.alert.show({content: '',});
} else if (this.holidayStart === "") {
this.$vux.alert.show({content: '',});
} else if (this.holidayEnd === "") {
this.$vux.alert.show({content: '',});
} else {
this.$router.push({
name: 'apply',
params: {
"hrWorkflowType": "",
"hrWorkflowTypeDetail": this.hrApplyType,//
"hrStartTime": this.holidayStart,//
"hrEndTime": this.holidayEnd,//
"hrWorkflowReason": this.holidayReason,//
"hrWorkflowNode": "1"
}
});
}
},
saveOrSubmit(stateFlag){
if (this.params.hrApplyType === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.params.holidayStart === "") {
this.$vux.alert.show({
content: '',
});
} else if (this.params.holidayEnd === "") {
this.$vux.alert.show({
content: '',
});
} else {
if (stateFlag === 'save') {
this.params.fid = "";
this.$vux.loading.show({text: ''});
} else {
this.$vux.loading.show({text: ''});
}

applyCreate({
"hrWorkflowId": this.params.hrWorkflowId,
"hrWorkflowType": "applyHoliday",
"hrWorkflowTypeDetail": this.params.hrApplyType,  //
"hrStartTime": this.params.holidayStart,         //
"hrEndTime": this.params.holidayEnd,             //
"hrWorkflowReason": this.params.holidayReason,   //
"hrWorkflowNode": this.params.fid,
"hrWorkflowNodeStatus": "1",
}).then(response => {
if (response.data && response.data.rows) {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: ''});
} else {
this.$vux.toast.show({text: ''});
window.localStorage.setItem('applyStatus', "2");
}
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
} else {
this.$vux.loading.hide();
if (stateFlag === 'save') {
this.$vux.toast.show({text: ''});
} else {
this.$vux.toast.show({text: ''});
}
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
revoke(){
this.$vux.toast.show({text: ''});
recall({"hrWorkflowId": this.params.hrWorkflowId}).then(response => {
if (response.data && response.data.success) {
this.$vux.loading.hide();
this.$vux.toast.show({text: ''});
window.localStorage.setItem('applyStatus', "1");
let _this = this;
setTimeout(function () {
_this.$router.go(-1);
}, 1600);
}
}).catch(error => {
this.$vux.loading.hide();
});
}
},
mounted() {
},
created() {

if (this.$route.query.item) {
let detailsItem = this.$route.query.item;
this.params.hrWorkflowId = detailsItem.hrWorkflowId;
this.params.hrApplyType = detailsItem.hrWorkflowTypeDetail;
this.params.holidayStart = detailsItem.hrStartTime;
this.params.holidayEnd = detailsItem.hrEndTime;
this.params.holidayReason = detailsItem.hrWorkflowReason;
}
if (this.$route.query.applyStatus === '1') {
this.applyStatus = "1";
} else if (this.$route.query.applyStatus === '2') {
this.applyStatus = "2";
} else if (this.$route.query.applyStatus === '3') {
this.applyStatus = "3";
}
}
}



